* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #2f4749;
  background: -webkit-linear-gradient(to right, #467e85, #2f4749);
  background: linear-gradient(to right, #467e85, #2f4749);
  min-width: 580px;
  height: 100vh;
}

.pixel {
  height: 30px;
  width: 30px;
  margin: 0px
}

.pixels {
  display: grid;
  grid-template-columns: repeat(19, 1fr);
  width: 210px;
  margin-right: 360px;
}

.color-pixel {
  height: 34px;
  width: 34px;
  margin: 0 0 40px 0;
  cursor: pointer;
}

h1 {
  text-shadow: 3px 0px 0px rgb(0, 0, 0);
  font-size: 40px;
  margin: 40px 0px 30px 0px;
  font-family: 'Finger Paint', cursive;
  cursor: pointer;
}
